import React from 'react';
import PropTypes from 'prop-types';
import { useStore } from '../store';
import People from './people';

function partial(props) {
    const { data, index, handleClick, selectedIndex, childRefs } = props;
    const { action, disp } = data;

    return (
        <li
            tabIndex={-1}
            id={`${index}`}
            ref={childRefs}
            key={`result-programs-${index}`}
            role={selectedIndex === index && 'gridcell'}
            aria-selected={selectedIndex === index}
            className="autocomplete-concierge__search-result-item"
            onClick={(e) => handleClick(e, action)}
            data-link={action}
        >
            <h4 className="title">
                {disp.title} &nbsp;
                {disp.metaData.programCredentialType && (
                    <span>({disp.metaData.programCredentialType})</span>
                )}
            </h4>

            <div className="details">
                {disp.metaData.programFaculty && (
                    <div className="autocomplete-results__metadata">
                        {disp.metaData.programFaculty}
                    </div>
                )}

                {disp.metaData.stencilsDeliveryMethod && (
                    <div className="autocomplete-results__metadata">
                        {disp.metaData.stencilsDeliveryMethod}
                    </div>
                )}
            </div>
        </li>
    );
}

export default function Programs(props) {
    const { results, selectedIndex } = useStore();
    const { id, label, className, templateId, childRefs } = props;

    const handleClick = (e, action) => {
        e.preventDefault();
        document.location.href = action;
    };

    if (!results[templateId] || results[templateId].length === 0) {
        return null;
    }

    return (
        <div
            id={`result-row-${id}`}
            className={[`autocomplete-concierge__section`, className].join(' ')}
            role="row"
        >
            <p className="autocomplete-concierge__section-title">{label}</p>
            <ul>
                {results[templateId] &&
                    results[templateId].map((data, index) =>
                        partial({
                            data,
                            index,
                            handleClick,
                            selectedIndex,
                            childRefs,
                        })
                    )}
            </ul>
        </div>
    );
}

const { string, number, func } = PropTypes;

People.propTypes = {
    id: string.isRequired,
    label: string,
    className: string,
    templateId: number.isRequired,
    childRefs: func.isRequired,
};

People.defaultProps = {
    label: ``,
    className: ``,
};
